import React, {
  useState,
  useEffect,
  useCallback
} from 'react'
import clsx from 'clsx'
import PerfectScrollbar from 'react-perfect-scrollbar'
import PropTypes from 'prop-types'
import {
  Box,
  Card,
  CardHeader,
  Divider,
  Table,
  TableBody,
  TableCell,
  TableRow,
  Typography,
  makeStyles
} from '@material-ui/core'
import { getMedicalTools } from 'src/services/borrowingHistoryService'
import useIsMountedRef from 'src/hooks/useIsMountedRef'
import CircularProgress from './CircularProgress'
import BorrowingStatusLabel from 'src/components/BorrowingStatusLabel'

const useStyles = makeStyles((theme) => ({
  root: {},
  image: {
    flexShrink: 0,
    height: 56,
    width: 56
  }
}));

function MostProfitableProducts({ borrowingTime, className, ...rest }) {
  const classes = useStyles()
  const isMountedRef = useIsMountedRef()
  const [equipments, setEquipments] = useState(null)

  const getEquipments = useCallback(async() => {
    const response = await getMedicalTools(borrowingTime.id)
    
    if(response.resultFlag){
      const dataItem = response.value.data;

      if(isMountedRef.current){
        setEquipments(()=> [...dataItem.medicalTools])
      }
    }

  }, [isMountedRef, borrowingTime])

  useEffect(() => {
    getEquipments()
  }, [getEquipments])

  if (!equipments) {
    return null;
  }

  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <CardHeader
        title="Medical Tools"
      />
      <Divider />
      <PerfectScrollbar>
        <Box minWidth={700}>
          <Table>
            <TableBody>
              {equipments.map((medicalTool) => {
                  const progress = (100 * medicalTool.returned) / medicalTool.count
                  return (
                    <TableRow
                    hover
                    key={medicalTool.id}
                    >
                    <TableCell>
                        <Box
                        display="flex"
                        alignItems="center"
                        >
                        <img
                            alt={medicalTool.name}
                            className={classes.image}
                            src={medicalTool.media}
                        />
                        <Box ml={2}>
                            <Typography
                            variant="body2"
                            color="textPrimary"
                            >
                            {medicalTool.name}
                            </Typography>                        
                        </Box>
                        </Box>
                    </TableCell>
                    <TableCell>
                        <BorrowingStatusLabel status={medicalTool.status} />
                    </TableCell>
                    <TableCell>
                        <Box
                        display="flex"
                        alignItems="center"
                        justifyContent="flex-end"
                        >
                        <Box mr={2}>
                            <Typography
                            align="right"
                            variant="h6"
                            color="textPrimary"
                            >
                            {medicalTool.returned}{' / '}{medicalTool.count}
                            </Typography>
                            <Typography
                            variant="body2"
                            color="textSecondary"
                            >
                            Returned
                            </Typography>
                        </Box>
                        <CircularProgress value={progress} />
                        </Box>
                    </TableCell>
                    </TableRow>
                )
              })}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
    </Card>
  )
}

MostProfitableProducts.propTypes = {
  className: PropTypes.string,
  borrowingTime: PropTypes.object
}

export default MostProfitableProducts