export const initError = ({errorCode, errorMessage, activityId}) => {
  const displayErrorCode =  errorCode ?? "something went wrong"
  const displayActivityId = activityId ?? ""
  return { 
    resultFlag: false, 
    errorCode: displayErrorCode,
    errorMessage: errorMessage,
    activityId: displayActivityId
  }
}

export const initResult = (value) => {
  if(value) 
    return { resultFlag: true, value: {...value} }
  else
    return { resultFlag: true }
}

export const accessResponse = (response) => {
  const { data, /*status, statusText, headers, config, request*/ } = response
  //console.log(response)
  const result = initResult(data)
  //console.log('result: ', result)
  return result
}

export const accessError = (error) => {
  if(error.response){
    // The request was made and the server responded with a status code
    // that falls out of the range of 2xx
    const { data, status, statusText, headers } = error.response
    // console.log("---------------------------------")
    // console.log(status)
    // console.log(data)
    // console.log(headers)
    // console.log("---------------------------------")

    if(!data){
      const result = initError({ errorMessage: `${status}: ${statusText}` })
      return result
    }
    else{
      const result = initError(data)
      return result
    }
  }
  else if(error.request){
    // The request was made but no response was received
    // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
    // http.ClientRequest in node.js
    console.error(error.request)
    debugger
    console.error(`Error: `, error.message)
    
    const result = initError({ errorMessage: error.message })
    return result
  }
  else{
    // Something happened in setting up the request that triggered an Error
    console.error(`Error: `, error.message)
    
    const result = initError({ errorMessage: error.message })
    return result
  }
}