import React, { useEffect, useState } from 'react'
import { useTranslation } from "react-i18next"
import { TRANSLATE_NS } from 'src/constants'
import useIsMountedRef from 'src/hooks/useIsMountedRef'
import Page from 'src/components/Page'
import { getById } from 'src/services/borrowingHistoryService'
import {
  Card,
  CardContent,
  Container,
  Grid,
  makeStyles, 
  Typography} from '@material-ui/core'
import Header from './Header'
import { useHistory } from 'react-router-dom'
import Equipments from './Equipments'
import SplashScreen from 'src/components/SplashScreen'
import Progress from './Progress'

const useStyles = makeStyles((theme)=> ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    padding: theme.spacing(2, 0),
  },
  card: {    
    marginRight: 16
  },
  
}))

function DetailView({match}){
  const classes = useStyles();
  const {t} = useTranslation([TRANSLATE_NS.BORROWING_HISTORY, TRANSLATE_NS.TRANSLATIONS])
  const isMountedRef = useIsMountedRef()
  const [dataItem, setDataItem] = useState(null)
  const history = useHistory()
  const borrowTimeId = match.params?.id
  
  useEffect(()=> {
    
    async function getDataFromApi() {
      const response = await getById(borrowTimeId)
      if(response.resultFlag){
        const dataItem = response.value.data;

        if(!dataItem){
          const url = `/404`;
          history.push(url);
        }
       
        if(isMountedRef.current){
          setDataItem(()=> ({...dataItem}))
        }
      }
    }

    if(borrowTimeId){
      getDataFromApi()
    }

  }, [isMountedRef, history, borrowTimeId])

  if(!dataItem){
    return <SplashScreen />
  }

  return (
    <Page
      className={classes.root}
      title={t("borrowHistory:view borrowing history details")}
    >
      <Container maxWidth="lg">
        <Header 
          mb={1}
          display="flex"
          borrowingTime={dataItem} 
        />
        <Grid 
          container
          display="flex"
          justify="center"
        >        
          <Grid item xs={12} md={3} >          
            <Grid item xs className={classes.card}>
              <Progress value={20} />
            </Grid>
            <Grid item xs className={classes.card} style={{marginTop: 8}}>
              <Card >
                <CardContent>
                  <Typography variant="body2" gutterBottom>
                    {'ยืมโดย : '}
                    {dataItem.borrowBy}
                  </Typography>
                  <Typography variant="body2" gutterBottom>
                    {'คืนโดย : '}
                    {dataItem.returnBy}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
          <Grid item xs={12} md={9}>
            <Equipments borrowingTime={dataItem} />
          </Grid>
        </Grid>        
      </Container>
    </Page>
  )

}

export default DetailView
