import React from 'react'
import clsx from 'clsx'
import PropTypes from 'prop-types'
import { Link as RouterLink } from 'react-router-dom'
import { Box, Button, Typography, makeStyles } from '@material-ui/core'
import {useTranslation} from "react-i18next"
import { TRANSLATE_NS } from 'src/constants'
import BorrowingStatusLabel from 'src/components/BorrowingStatusLabel'

const useStyles = makeStyles((theme)=> ({
  root: {},  
  label: {
    marginLeft: theme.spacing(2)
  },
}))

function Header({borrowingTime, className, ...rest}){
  const classes = useStyles()
  const {t} = useTranslation([TRANSLATE_NS.TRANSLATIONS, TRANSLATE_NS.BORROWING_HISTORY])
  
  return (
    <Box
      className={clsx(className, classes.root)}
      {...rest}
    >
      <Box 
        flexGrow={1}
      >
        <Typography 
          variant="body2" 
          component="div"
          gutterBottom
        >
          <Box 
            fontWeight={500} 
            ml={1} 
            color="textPrimary" 
            component="p" 
            display="inline"
            fontSize="h1.fontSize"
          >
            {borrowingTime?.surgicalType}
          </Box>
          <BorrowingStatusLabel 
            status={borrowingTime?.status} 
            className={classes.label}
          />
        </Typography>
      </Box>          
      <Button
        variant="outlined"
        size="medium"
        color="secondary"
        component={RouterLink}
        to="/borrowing-history"
      >
        {t('translations:exit')}
      </Button>
    </Box>
  )
}

Header.propTypes = {
  className: PropTypes.string,
  borrowingTime: PropTypes.shape({
    id: PropTypes.string,
    surgicalType: PropTypes.string,
    medicalToolsCount: PropTypes.number,
    medicalItemsCount: PropTypes.number,
    borrowTimestamp: PropTypes.number,
    borrowBy: PropTypes.string,
    returnTimestamp: PropTypes.number,
    returnBy: PropTypes.string,
    status: PropTypes.string,
    createdTimestamp: PropTypes.number,
    createdBy: PropTypes.string,
  })
}

export default Header