import axios from 'src/utils/axios'
import * as handleResponse from './response'

export const getList = ({filters, sort, page, limit}) => new Promise((resolve, reject) => {  
  axios.get('/api/borrowing-history/fetch', { params: {  sort, page, limit, filters} 
  })
  .then((response)=> {
    const result = handleResponse.accessResponse(response)
    resolve(result)
  })
  .catch((error) => {
    const result =  handleResponse.accessError(error)
    reject(result)
  })
})

export const getById = (borrowTimeId) => new Promise((resolve, reject) => {
  axios.get('/api/borrowing-history/getById', { params: { 
    id: borrowTimeId } 
  })
  .then((response)=> {
    const result = handleResponse.accessResponse(response)
    resolve(result)
  })
  .catch((error) => {
    const result =  handleResponse.accessError(error)
    reject(result)
  })
})


