import React from 'react'
import clsx from 'clsx'
import PropTypes from 'prop-types'
import Label from './Label'
import {
  makeStyles
} from '@material-ui/core'

const useStyles = makeStyles(() => ({
  root: {}
}))

const BorrowingStatusLabel = ({ status, className, ...rest}) => {
  const classes = useStyles()
  let color = ""
  switch (status) {
    case 'Borrowed':
      color = "warning"
      break
    case 'Returned':
      color = "success"
      break
    case 'Created':
      color = "secondary"
      break        
    default: break;
  }
  return (
    <Label
      color={color}
      className={clsx(classes.root, className)}
      {...rest}
    >
      {status}
    </Label>
  )
}

BorrowingStatusLabel.propTypes = {
  className: PropTypes.string,
  status: PropTypes.string.isRequired
}

export default BorrowingStatusLabel
