import React from 'react'
import clsx from 'clsx'
import PropTypes from 'prop-types'
import { Card, CardActions, CardContent, Grid, makeStyles, Typography } from '@material-ui/core'

const useStyles = makeStyles((theme) => ({
  root: {
  },
  content: {
    display: 'flex',
    justifyContent: 'center'
  },
  wrapper: {
    width: 120,
    height: 120
  },
  circle1: {
    stroke: theme.palette.type === 'dark' ? 'rgba(0,0,0,0.15)' : 'rgba(0,0,0,0.05)',
    fill: 'none',
    strokeWidth: 4
  },
  circle2: {
    stroke: theme.palette.secondary.main,
    fill: 'none',
    strokeWidth: 4,
    animation: '$progress 1s ease-out forwards'
  },
  '@keyframes progress': {
    '0%': {
      strokeDasharray: '0 100'
    }
  },
  value: {
    marginLeft: 50,
    marginTop: -72
  }
}));

function CircularProgress({ value, className, ...rest }) {
  const classes = useStyles()

  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      {/* <CardHeader title="Returned" />
      <Divider light /> */}
      <CardContent className={classes.content}>
        <div className={classes.wrapper}>
          <svg viewBox="0 0 36 36">
            <path
              className={classes.circle1}
              d="M18 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
              strokeDasharray="100, 100"
            />
            <path
              className={classes.circle2}
              d="M18 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
              strokeDasharray={`${value}, 100`}
            />
          </svg>
          <Typography variant="body2" className={classes.value}> 
            {value}
          </Typography>
        </div>
      </CardContent>
      <CardActions>
        <Grid 
          container
          display="flex"
        >
          <Grid item container xs direction="column" style={{textAlign: 'center'}}>
            <Grid item>
              <Typography variant="body2">
                {20}
              </Typography>
            </Grid>
            <Grid item>
              <Typography variant="body2">
                {"Returned"}
              </Typography>
            </Grid>
          </Grid>
          <Grid item container xs direction="column" style={{textAlign: 'center'}}>          
            <Grid item>
              <Typography variant="body2">
                {80}
              </Typography>
            </Grid>
            <Grid item>
              <Typography variant="body2">
                {"Borrowed"}
              </Typography>
            </Grid>
          </Grid>
        </Grid>
      </CardActions>
    </Card>
  )
}

CircularProgress.propTypes = {
  className: PropTypes.string,
  value: PropTypes.number.isRequired
}

export default CircularProgress